.icon {
   & img {
      position: absolute;
      size: 25px;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
          -ms-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
   }
}
.img-wrap {
